import React from 'react';
import {Component} from 'react';
import RSVPGuest from './RSVPGuest';
import Jsonp from '../Jsonp';

export default class RSVPForm extends Component{

    constructor(props) {
        super(props);

        this.props = props;
        this.state = {
            lookupInfo: {
                email: null,
                firstName: null,
                lastName: null
            },
            canLookup: false,
            loading: false,
            guestDetails: null,
            requiredFieldsComplete: false,
            errors: null,
            successfulRSVP: false
        };
        this.targetUrl = "https://script.google.com/macros/s/AKfycbzX1W2zdJTiCXC5cuRTx-H8uyup8m806iZJS0a5EX_4QQpBwilOazUwhT98uhiroWTtEw/exec"

        this.handleLookupChange = this.handleLookupChange.bind(this);
        this.canLookup = this.canLookup.bind(this);
        this.lookupRSVPInfo = this.lookupRSVPInfo.bind(this);
        this.processLookupResponse = this.processLookupResponse.bind(this);
        this.updateGuestDetail = this.updateGuestDetail.bind(this);
        this.submitRSVP = this.submitRSVP.bind(this);
        this.updateFields = this.updateFields.bind(this);
        this.displayBasicInfo = this.displayBasicInfo.bind(this);
    }

    handleLookupChange(field, event) {
        this.setState(state => {
            let lookupInfo = state.lookupInfo;
            if (event && event.target){
                lookupInfo[field] = event.target.value
            }
            return {lookupInfo: lookupInfo, canLookup: this.canLookup()};
        });
    }

    canLookup() {
        return this.state.lookupInfo.email || (this.state.lookupInfo.firstName && this.state.lookupInfo.lastName);
    }

    lookupRSVPInfo() {
        this.setState({loading: true}, () => {
            let postBody = JSON.stringify({
                "sessionId": this.props.sessionId,
                "method": "getGuestInfo",
                "parameters": this.state.lookupInfo
            });
            let targetUrl = this.targetUrl + "?data=" + encodeURIComponent(postBody);
            Jsonp(targetUrl, response => this.processLookupResponse(response));
        });
    }

    updateGuestDetail(id, field, value){
        this.setState(state => {
            const details = state.guestDetails;
            const guest = details.find(guest => guest.id === id);
            guest[field]=value;
            setTimeout(this.updateFields, 10);
            return {guestDetails: details};
        });
    }

    updateFields() {
        this.setState(state => {
            const details = state.guestDetails;
            const requiredFields = []
            this.props.content.detailsSchema.forEach(field => {
                if (field.required){
                    requiredFields.push(field.field);
                }
            });
            let requiredFieldsComplete = true;
            details.forEach( guest => {
                requiredFieldsComplete = requiredFields.reduce((accumulator, field) => {
                    return accumulator && guest[field] && ((guest[field]+"").trim() !== "");}, requiredFieldsComplete);
                requiredFieldsComplete = requiredFieldsComplete || guest.attending === "false"
                guest["pronounsNotListed"] = guest["pronouns"] === "NotListed";
                if (!guest["pronounsNotListed"]) {
                    guest.addPronouns = "";
                }
            });
            return {guestDetails: details, requiredFieldsComplete: requiredFieldsComplete}
        });
    }

    processLookupResponse(response) {
        const data = response;
        const guests = [];
        const errors = [];
        const schema = this.props.content.detailsSchema;
        let foundGuest = false;
        for(let key in data.guestInfo) {
            foundGuest = true;
            const guestAry = data.guestInfo[key];
            const guest = {"id": key}
            for(let index in guestAry){
                if (!schema[index]){
                    continue;
                }
                guest[schema[index]["field"]]=guestAry[index];
            }
            if (!guest.preferredName){
                guest.preferredName = guest.firstName + " " + guest.lastName;
            }
            guests.push(guest);
        }
        if (!foundGuest){
            errors.push("This information does not match our records. Try entering your information again or call Alex or Erin to resolve.");
        }
        this.setState({guestDetails: guests, errors: errors, loading: false});
        this.updateFields();
    }

    submitRSVP() {
        const details = this.state.guestDetails;
        const rsvpObject = {};
        details.forEach(guest => {
            const guestAry = [];
            this.props.content.detailsSchema.forEach(field => {
                guestAry.push(guest[field.field]);
            });
            rsvpObject[guest.id] = guestAry;
        });
        const rsvp = JSON.stringify(rsvpObject);
        this.setState({loading: true}, () => {
            let postBody = JSON.stringify({
                "sessionId": this.props.sessionId,
                "method": "rsvpGuests",
                "parameters": rsvp
            });
            let targetUrl = this.targetUrl+ "?data=" + encodeURIComponent(postBody);
            Jsonp(targetUrl, response => {
                //const data = JSON.parse(response);
                this.setState({successfulRSVP: true, loading: false});
            });
        });
    }

    displayBasicInfo(){
        if (this.state.loading){
            return false;
        }
        return this.state.guestDetails === null 
        || (this.state.errors && this.state.errors.length > 0 );
    }

    componentDidMount() {
        
    }

    render() {
        return (
        <div className="section">
            <h2 className='sectionHeader'>{this.props.content.sectionTitle}</h2>
            <div className={this.state.loading ? 'sectionContent loader' : 'sectionContent'}> 
                <div className="preamble">
                    <div className="line line1">
                        {this.props.content.preambleLine1}
                    </div>
                    <div className="line line2">
                        {this.props.content.preambleLine2}
                    </div>
                </div>
                <div className="errorInfo">
                    {this.state.errors ? this.state.errors.map(error => (<div key={error}>{error}</div>)) : (null)}
                </div>
                <div className={this.displayBasicInfo() ? 'basicInfo' : 'basicInfo hide'}>
                    <div className="field">
                        <label htmlFor="email">Email address</label>
                        <input type="text" id="email" onChange={ event => { event.persist(); this.handleLookupChange("email", event)}} />
                    </div>
                    <h6>- Or -</h6>
                    <div className="multiField">
                        <label htmlFor="firstName">Name</label>
                        <div className="subField">
                            <input type="text" id="firstName" onChange={ event => { event.persist(); this.handleLookupChange("firstName", event)}} />
                            <label htmlFor="firstName">First name</label>
                        </div>
                        <div className="subField">
                            <input type="text" id="lastName" onChange={ event => { event.persist(); this.handleLookupChange("lastName", event)}} />
                            <label htmlFor="lastName">Last name</label>
                        </div>
                    </div>
                    <button disabled={!this.state.canLookup} className="submitButton" id="getRSVPInfoButton" onClick={this.lookupRSVPInfo}>Next</button>
                </div>
                <div className={this.state.guestDetails === null || this.state.successfulRSVP || this.state.loading? 'rsvpForm hide' : 'rsvpForm'}>
                    {this.state.guestDetails !== null ? this.state.guestDetails.map(guest => (<RSVPGuest key={guest.id} schema={this.props.content.detailsSchema} guest={guest} onChange={this.updateGuestDetail} />)) : (null)}
                    <button disabled={!this.state.requiredFieldsComplete} className="submitButton" onClick={this.submitRSVP}>RSVP</button>
                </div>
                <div className={this.state.successfulRSVP ? "submittedForm" : "submittedForm hide"}>
                    <h4>Thank you for your response!</h4>
                </div>
            </div>
        </div>
        );
    }
};