import React from 'react';
import {Component} from 'react';
import Jsonp from '../Jsonp';

export default class SignOn extends Component {
    constructor(props){
        super(props);

        this.state = { password: '', loading: false, error: false};

        this.onKeyDown = this.onKeyDown.bind(this);
        this.updatePassword = this.updatePassword.bind(this);
        this.authenticate = this.authenticate.bind(this);
        this.processAuthResponse = this.processAuthResponse.bind(this);

    }

    onKeyDown(event) {
        if(event.keyCode === 13){
            this.authenticate();
        }
    }

    updatePassword(event) {
        this.setState({password: event.target.value});
    }

    processAuthResponse(response) {
        console.log(response);
        if (response.sessionId) {
            this.props.onSessionChange(response.sessionId);
        } else {
            this.setState({loading: false, error: true});
        }
    }

    authenticate(){
        this.setState({loading: true}, () => {
            const targetUrl = 'https://script.google.com/macros/s/AKfycbzX1W2zdJTiCXC5cuRTx-H8uyup8m806iZJS0a5EX_4QQpBwilOazUwhT98uhiroWTtEw/exec'
             + '?data=' + encodeURIComponent(JSON.stringify({
                 method: "authenticate",
                 password: this.state.password
             }));
            Jsonp(targetUrl, (response) => { this.processAuthResponse(response) });
        });

    }

    render() {
        return (<div className="authenticatePage">
            <div className="inviteForm">
                <div className="navBar">
                    Invitation Code
                </div>
                <div className={this.state.loading ? "sectionContent loader" : "sectionContent"}>
                    {this.state.error && <div className="error">*Invalid Invitation Code</div> }
                    <div className="field">
                        <input type="password" id="password" className="password" 
                            onChange={this.updatePassword} onKeyDown={this.onKeyDown} />
                    </div>
                    <div>
                        <button id="authenticate" className="submitButton" onClick={this.authenticate}>Enter</button>
                    </div>
                </div>
            </div>
        </div>);
    }
}