import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Header from './components/Header.js';
import './App.css';
//import PageData from './ww.json';
import Page from './components/Page.js';
import SignOn from './components/SignOn.js';
import Jsonp from './Jsonp.js';

export default class App extends Component {
  constructor(props) {
    super(props);
    const sessionId = window.localStorage.getItem('sessionId');
    this.state = {
      sessionId: sessionId,
      PageData: { "pages": [] }
    }
  }

  onSessionChange(sessionId) {
    let targetUrl = 'https://script.google.com/macros/s/AKfycbzX1W2zdJTiCXC5cuRTx-H8uyup8m806iZJS0a5EX_4QQpBwilOazUwhT98uhiroWTtEw/exec?data='
      + encodeURIComponent(JSON.stringify({
        method: "config",
        sessionId: sessionId
      }));
    Jsonp(targetUrl, response => {
      this.setState({sessionId:sessionId, PageData: response});
    });
  }

  render() {
    if (!this.state.sessionId) {
      return (<SignOn onSessionChange={ (sessionId) => this.onSessionChange(sessionId)} />);
    }
    console.log(this.state);
    return (
      <Router>
        <Header pages={this.state.PageData} />
        <Switch>
          <Route exact path={process.env.PUBLIC_URL + "/"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/")} sessionId={this.state.sessionId} />
          </Route> 
          <Route exact path={process.env.PUBLIC_URL + "/gallery"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/gallery")} sessionId={this.state.sessionId} />
          </Route> 
          <Route exact path={process.env.PUBLIC_URL + "/faqs"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/faqs")} sessionId={this.state.sessionId} />
          </Route> 
          <Route exact path={process.env.PUBLIC_URL + "/registry"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/registry")} sessionId={this.state.sessionId} />
          </Route> 
          <Route exact path={process.env.PUBLIC_URL + "/stpaul"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/stpaul")} sessionId={this.state.sessionId} />
          </Route>
          <Route exact path={process.env.PUBLIC_URL + "/rsvp"}>
            <Page pageData={this.state.PageData.pages.find(page => page.path === "/rsvp")} sessionId={this.state.sessionId} />
          </Route> 
        </Switch>
      </Router>
    );
  }
}
